/* ===================================
========= register page  */
.form-container {
    margin-top: 60px;
    display: flex;
    align-items: center;
    justify-content: center;
    
    min-height: 100vh;
    width: 100%;
    flex-direction: column;
    background-color: #ffdee9;
    background-image: linear-gradient(0deg, #0e50c2 0%, #12d764 100%);
  }
  
 
  .form-container form {
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    padding: 20px;
    width: 80%;
    background-color: #fff;
  }
  
  .form-container form .title {
    text-align: center;
    margin-bottom: 15px;
    font-weight: bold;
    font-family: "Playfair Display", serif;
  }
  
  .form-container form input,.form-container form select {
    border: none;
    border-bottom: 1px solid #000;
    border-radius: 0;
    padding: 15px;
    
  }
