  .forgot-btn {
    background-color: #434343a4 !important;
    border: 0 !important;
  }
  
  .forgot-btn:hover {
    color: white !important;
  }
  .slider {
    width: 100%;
    cursor: pointer;
    height: 25px;
    background: linear-gradient(to right, #AED9E0, #B8E1F2); /* Light blue to light green gradient */
    border-radius: 10px;
    outline: none; /* Removes the default focus outline */
    border: 1px solid #aaa; /* Adds a border to the slider track */
  }
  
  .slider::-webkit-slider-thumb {
    cursor: pointer;
  }
  
  .sliderContainer {
    padding: 20px;
  }
  
  .sliderValue {
    text-align: center;
  }
  